.Education {
    padding-block: 1rem;
    background-color: var(--color-light);
  }
  
  .timeline {
    position: relative;
    box-sizing: border-box;
    width: 1000px;
    margin: 3rem auto;
    
    
  }
  
  .timeline:before {
    position: absolute;
    left: 50%;
    width: 2px;
    height: 100%;
  
    content: "";
  
    background: #c5c5c5;
  }
  
  .timeline ul {
    margin: 0;
    padding: 0;
  }
  
  @media (max-width: 1000px) {
    .timeline {
      width: 100%;
      margin-top: 5rem;
    }
  }
  
  @media (max-width: 767px) {
    .timeline {
      width: 100%;
      padding-bottom: 0;
    }
  
    .timeline:before {
      left: 20px;
      height: 100%;
    }
  }
  